import { Component, OnInit, ViewChild, ElementRef } from "@angular/core";
import { TablaetiquetadoService } from "../tablaetiquetado.service";
import * as jsPDF from "jspdf";
import { parse } from "../../../node_modules/json2csv";
import { saveAs } from "node_modules/file-saver/src/FileSaver";

@Component({
  selector: "app-tablaetiquetado",
  templateUrl: "./tablaetiquetado.component.html",
  styleUrls: ["./tablaetiquetado.component.css"],
})
export class TablaetiquetadoComponent implements OnInit {
  datos = [];
  total = "0.0";
  constructor(private tablaEtiquetadoService: TablaetiquetadoService) {
    this.tablaEtiquetadoService.getData().subscribe(
      (data) => {
        this.format(data);
      },
      (error) => {
        console.error(error);
      }
    );
  }
  format(datos) {
    let total = 0.0;
    datos.map((elemento) => {
      const { etiquetado, fondo, monto_anual } = elemento;
      total += monto_anual;
      this.datos.push({
        monto_anual: new Intl.NumberFormat().format(monto_anual),
        etiquetado,
        fondo,
      });
    });
    this.total = new Intl.NumberFormat().format(total);
  }
  ngOnInit(): void {}
  @ViewChild("content") content: ElementRef;
  public SavePDF(): void {
    let content = this.content.nativeElement;
    let doc = new jsPDF();
    let _elementHandlers = {
      "#editor": function (element, renderer) {
        return true;
      },
    };
    doc.fromHTML(content.innerHTML, 5, 5, {
      width: 190,
      elementHandlers: _elementHandlers,
    });
    doc.save("PaqueteEtiquetado.pdf");
  }
  public SaveCSV(): void {
    const fields = ["fondo", "montoa", "etiquetado"];
    const headers = { fields };
    const csv = parse(this.datos, headers);
    let blob = new Blob([csv], { type: "text/csv" });
    saveAs(blob, "Tabla_Etiquetado.csv");
  }
}
