import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Router } from "@angular/router";

@Injectable({
  providedIn: "root",
})
export class AuthService {
  //private URL = "http://200.33.114.47:3000/api/credenciales"; //antes de subir al servidor, tengo que descomentar esta ruta y comentar la siguiente  192.168.101.136:3000 http://200.33.114.47:3000
  private URL = "http://localhost:3000/api/credenciales";
  //cuando este trabando en mi maquina debo usar esta ruta
  constructor(private http: HttpClient, private router: Router) {}

  inicio(usuario) {
    return this.http.post<any>(this.URL, usuario);
  }

  logeado() {
    if (localStorage.getItem("Id")) return true;
    else return false;
  }

  logout() {
    if (localStorage.getItem("Id")) {
      localStorage.removeItem("Id");
      localStorage.removeItem("Rol");
      this.router.navigate(["/login"]);
    }
  }
}
