import { Component, OnInit } from "@angular/core";

@Component({
  selector: "app-dash2",
  templateUrl: "./dash2.component.html",
  styleUrls: ["./dash2.component.css"],
})
export class Dash2Component implements OnInit {
  constructor() {}

  ngOnInit(): void {}
  vistaAdmin() {
    if (localStorage.getItem("Rol") == "Admin") return true;
    else return false;
  }
  vistaUsuario() {
    if (localStorage.getItem("Rol") == "Usuario") return true;
    else return false;
  }
}
