import { Component, OnInit } from "@angular/core";
import { AuthService } from "../services/auth.service";

@Component({
  selector: "app-menu-estatico",
  templateUrl: "./menu-estatico.component.html",
  styleUrls: ["./menu-estatico.component.css"],
})
export class MenuEstaticoComponent implements OnInit {
  txtlogeo = this.logueado() ? "Salir" : "Entrar";
  ngOnInit(): void {}

  constructor(private authService: AuthService) {
    this.txtlogeo = this.logueado() ? "Salir" : "Entrar";
  }
  logout() {
    if (this.logueado()) {
      this.txtlogeo = "Entrar";
      this.authService.logout();
      window.location.href = "http://presupuestoabierto.guanajuato.gob.mx/";
    }
  }
  logueado() {
    if (this.authService.logeado()) {
      this.txtlogeo = "Salir";
      return true;
    } else {
      this.txtlogeo = "Entrar";
      return true;
    }
  }
  onNavigate() {
    window.location.href = "https://twitter.com/sfiagto?lang=es";
  }
}
