import { Component, OnInit, ViewChild, ElementRef } from "@angular/core";
import * as jsPDF from "jspdf";
import { Articulo8Service } from "../articulo8.service";
import { parse } from "../../../node_modules/json2csv";
import { saveAs } from "node_modules/file-saver/src/FileSaver";

@Component({
  selector: "app-articulo8",
  templateUrl: "./articulo8.component.html",
  styleUrls: ["./articulo8.component.css"],
})
export class Articulo8Component implements OnInit {
  //resetFormSubject: Subject<boolean> = new Subject<boolean>();

  datos1 = [];
  total1 = "0.0";
  datos2 = [];
  total2 = "0.0";
  datos3 = [];
  total3 = "0.0";

  total4 = "0.0";
  constructor(private articulo8Service: Articulo8Service) {
    this.articulo8Service.getData().subscribe(
      (data) => {
        this.format(data);
      },
      (error) => {
        console.error(error);
      }
    );
  }

  ngOnInit(): void {}
  @ViewChild("content") content: ElementRef;
  public SavePDF(): void {
    let content = this.content.nativeElement;
    let doc = new jsPDF();
    let _elementHandlers = {
      "#editor": function (element, renderer) {
        return true;
      },
    };
    doc.fromHTML(content.innerHTML, 5, 5, {
      width: 190,
      elementHandlers: _elementHandlers,
    });
    doc.save("OrganismosAutonomos.pdf");
  }
  public SaveCSV(): void {
    const fields = ["Nombre", "Centro", "monto_anual"];
    const headers = { fields };
    const csv = parse(this.datos1, headers);
    let blob = new Blob([csv], { type: "text/csv" });
    saveAs(blob, "OrganismosAutonomos.csv");
  }
  public SaveDocx(): void {
    const fields = ["Nombre", "Centro", "monto_anual"];
    const headers = { fields };
    const docx = parse(this.datos1, headers);
    let _elementHandlers = {
      "#editor": function (element, renderer) {
        return true;
      },
    };
    function exportNote() {
      var blob = new Blob([document.getElementById("editor").innerHTML]);
      saveAs("OrganismosAutonomos.docx");
    }
  }
  format(datos) {
    let total1 = 0.0;
    let total2 = 0.0;
    let total3 = 0.0;
    let total4 = 0.0;
    let monto = 0.0;
    datos.map((elemento) => {
      const {
        Centro,
        Nombre,
        monto_anual,
        Ramo_Admon,
        Denominacion,
        Unidad,
        Entidad,
      } = elemento;
      monto = monto_anual;
      if (
        Centro !== undefined &&
        Nombre !== undefined &&
        monto_anual !== undefined
      ) {
        this.datos1.push({
          Centro,
          Nombre,
          monto_anual: new Intl.NumberFormat().format(monto),
          monto_anual_crudo: monto_anual,
        });
        total1 += monto;
      }
      if (
        Ramo_Admon !== undefined &&
        Denominacion !== undefined &&
        monto_anual !== undefined
      ) {
        this.datos2.push({
          Ramo_Admon,
          Denominacion,
          monto_anual: new Intl.NumberFormat().format(monto),
          monto_anual_crudo: monto_anual,
        });
        total2 += monto;
      }
      if (
        Entidad !== undefined &&
        Unidad !== undefined &&
        monto_anual !== undefined
      ) {
        this.datos3.push({
          Unidad,
          Entidad,
          monto_anual: new Intl.NumberFormat().format(monto),
          monto_anual_crudo: monto_anual,
        });
        total3 += monto;
      }
    });
    total4 = total1 + total2 + total3;
    this.total1 = new Intl.NumberFormat().format(total1);
    this.total2 = new Intl.NumberFormat().format(total2);
    this.total3 = new Intl.NumberFormat().format(total3);
    this.total4 = new Intl.NumberFormat().format(total4);
  }
}
