import { Component, OnInit, ViewChild, ElementRef } from "@angular/core";
import { EtiquetadotablasService } from "../etiquetadotablas.service";
import * as jsPDF from "jspdf";
import { parse } from "../../../node_modules/json2csv";
import { saveAs } from "node_modules/file-saver/src/FileSaver";
import { Subject, from } from "rxjs";
import { Data } from "@angular/router";

@Component({
  selector: "app-teblaetiquetadodos",
  templateUrl: "./teblaetiquetadodos.component.html",
  styleUrls: ["./teblaetiquetadodos.component.css"],
})

//resetFormSubject: Subject<boolean> = new Subject<boolean>();
export class TeblaetiquetadodosComponent implements OnInit {
  datos = [];
  total = "0.0";
  constructor(private etiquetadotablasService: EtiquetadotablasService) {
    this.etiquetadotablasService.getData().subscribe(
      (data) => {
        this.agrupacion(data);
      },
      (error) => {
        console.error(error);
      }
    );
  }

  ngOnInit(): void {}
  @ViewChild("content") content: ElementRef;
  public SavePDF(): void {
    let content = this.content.nativeElement;
    let doc = new jsPDF();
    let _elementHandlers = {
      "#editor": function (element, renderer) {
        return true;
      },
    };
    doc.fromHTML(content.innerHTML, 5, 5, {
      width: 190,
      elementHandlers: _elementHandlers,
    });
    doc.save("TablaEtiquetado.pdf");
  }
  public SaveCSV(): void {
    const fields = ["fondo", "monto_anual", "etiquetado"];
    const headers = { fields };
    const csv = parse(this.datos, headers);
    let blob = new Blob([csv], { type: "text/csv" });
    saveAs(blob, "TablaEtiquetado.csv");
  }

  agrupacion(datos) {
    let total = 0.0;
    let vEtiquetado = 0.0;
    let noEtiquetado = 0.0;
    datos.map((elemento) => {
      const { monto_anual, etiquetado } = elemento;

      total += parseFloat(monto_anual);

      if (etiquetado == "Etiquetado") vEtiquetado += parseFloat(monto_anual);

      if (etiquetado == "No Etiquetado")
        noEtiquetado += parseFloat(monto_anual);
    });

    this.total = new Intl.NumberFormat().format(total);

    this.datos.push(
      {
        etiquetado: "Etiquetado",
        monto_anual: new Intl.NumberFormat().format(vEtiquetado),
      },
      {
        etiquetado: "No Etiquetado",
        monto_anual: new Intl.NumberFormat().format(noEtiquetado),
      }
    );
  }
}
