import { Component, OnInit, ViewChild, ElementRef } from "@angular/core";
import * as jsPDF from "jspdf";
import { TablafondeoService } from "../tablafondeo.service";
import { parse } from "../../../node_modules/json2csv";
import { saveAs } from "node_modules/file-saver/src/FileSaver";

@Component({
  selector: "app-tablafondeo",
  templateUrl: "./tablafondeo.component.html",
  styleUrls: ["./tablafondeo.component.css"],
})
export class TablafondeoComponent implements OnInit {
  datos = [];
  total = "0.0";

  constructor(private tablaFondeo: TablafondeoService) {
    this.tablaFondeo.getData().subscribe(
      (data) => {
        this.format(data);
      },
      (error) => {
        console.error(error);
      }
    );
  }
  format(datos) {
    let total = 0.0;
    datos.map((elemento) => {
      const { clave_fondo, monto_anual } = elemento;
      total += monto_anual;
      this.datos.push({
        monto_anual: new Intl.NumberFormat().format(monto_anual),
        clave_fondo,
      });
    });
    this.total = new Intl.NumberFormat().format(total);
  }

  ngOnInit(): void {}
  @ViewChild("content") content: ElementRef;
  public SavePDF(): void {
    let content = this.content.nativeElement;
    let doc = new jsPDF();
    let _elementHandlers = {
      "#editor": function (element, renderer) {
        return true;
      },
    };

    doc.fromHTML(content.innerHTML, 5, 5, {
      width: 190,
      elementHandlers: _elementHandlers,
    });
    doc.save("Tablero_Fondeo.pdf");
  }

  public SaveCSV(): void {
    const fields = ["clave_fondo", "monto_anual"];
    const headers = { fields };
    const csv = parse(this.datos, headers);
    let blob = new Blob([csv], { type: "text/csv" });
    saveAs(blob, "Tablero_Fondeo.csv");
  }
}
