import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";

@Injectable({
  providedIn: "root",
})
export class Articulo8Service {
  //private URL = "http://200.33.114.47:3000/api/tabla/art8";
  private URL = "http://localhost:3000/api/tabla/art8";
  constructor(private http: HttpClient) {}

  getData() {
    return this.http.get(this.URL);
  }
}
