import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";

@Injectable({
  providedIn: "root",
})
export class GastoPresupuestoService {
  //private URL = "http://200.33.114.47:3000/api/tabla/gasto-presupuesto";
  private URL = "http://localhost:3000/api/tabla/gasto-presupuesto";
  constructor(private http: HttpClient) {}

  getData() {
    return this.http.get<any>(this.URL);
  }
}
