import { Component, OnInit, ViewChild, ElementRef } from "@angular/core";
import * as jsPDF from "jspdf";
import { PaqueteFiscalService } from "../paquete-fiscal.service";
import { parse } from "../../../node_modules/json2csv";
import { saveAs } from "node_modules/file-saver/src/FileSaver";

@Component({
  selector: "app-paquete-fiscal",
  templateUrl: "./paquete-fiscal.component.html",
  styleUrls: ["./paquete-fiscal.component.css"],
})
export class PaqueteFiscalComponent implements OnInit {
  datos = [];
  total = "0.0";
  constructor(private paqueteFiscalService: PaqueteFiscalService) {
    this.paqueteFiscalService.getData().subscribe(
      (data) => {
        this.format(data);
      },
      (error) => {
        console.error(error);
      }
    );
  }

  format(datos) {
    let total = 0.0;
    datos.map((elemento, id) => {
      const { ramo, monto_anual } = elemento;
      total += monto_anual;
      this.datos.push({
        ramo,
        monto_anual: new Intl.NumberFormat().format(monto_anual),
        monto_anual_crudo: monto_anual,
      });
    });
    this.total = new Intl.NumberFormat().format(total);
  }

  ngOnInit(): void {}
  @ViewChild("content") content: ElementRef;
  public SavePDF(): void {
    let content = this.content.nativeElement;
    let doc = new jsPDF();
    let _elementHandlers = {
      "#editor": function (element, renderer) {
        return true;
      },
    };

    doc.fromHTML(content.innerHTML, 5, 5, {
      width: 190,
      elementHandlers: _elementHandlers,
    });
    doc.save("PaqueteFiscal.pdf");
  }

  public SaveCSV(): void {
    const fields = ["ramo", "monto_anual"];
    const headers = { fields };
    const csv = parse(this.datos, headers);
    let blob = new Blob([csv], { type: "text/csv" });
    saveAs(blob, "Tabla_Paquete_Fiscal.csv");
  }
}
