import { Component, OnInit, ViewChild, ElementRef } from "@angular/core";
import { GastoPresupuestoService } from "../services/gasto-presupuesto.service";
import * as jsPDF from "jspdf";
import { parse } from "../../../node_modules/json2csv";
import { saveAs } from "node_modules/file-saver/src/FileSaver";

import { ChartType, ChartOptions, ChartDataSets } from "chart.js";
import { Color, BaseChartDirective, Label } from "ng2-charts";

@Component({
  selector: "app-tabla-presupuesto",
  templateUrl: "./tabla-presupuesto.component.html",
  styleUrls: ["./tabla-presupuesto.component.css"],
})
export class TablaPresupuestoComponent implements OnInit {
  constructor(private gastoPresupuestoService: GastoPresupuestoService) {
    this.gastoPresupuestoService.getData().subscribe(
      (data) => {
        this.format(data);
      },
      (error) => {
        console.error(error);
      }
    );
  }
  datos = [];
  total = "0.0";
  etiquetas = [];
  valores = [];
  items = [];
  selected = [];
  @ViewChild("content") content: ElementRef;

  // Trozos del pastel - Etiquetas
  public chartLabels: Label[] = this.etiquetas;

  // Trozos del pastel - Cantidades
  public chartData: number[] = this.valores;

  // Trozos del pastel - Colores
  public chartColors = [
    {
      backgroundColor: [
        "rgba(26,151,228,1)",
        "rgba(17,116,225,1)",
        "rgba(6, 124, 197,1)",
        "rgba(3,121,194,1)",
        "rgba(33,114,165, 1)",
        "rgba(17,68, 99,1)",
        "rgba(25, 54, 76, 1)",
      ],
    },
  ];

  // Opciones de la grfica
  public chartOptions: ChartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    title: {
      text: "",

      display: true,
    },
    legend: {
      position: "bottom",
    },
    plugins: {
      datalabels: {
        formatter: (value, ctx) => {
          const label = ctx.chart.data.labels[ctx.dataIndex];
          return new Intl.NumberFormat().format(label);
        },
        color: "#fff",
      },
    },
  };

  public chartType: ChartType = "bar"; //types of graphics[pie,bar,line]
  public chartLegend = true;
  @ViewChild(BaseChartDirective) grafica: BaseChartDirective;
  format(datos) {
    let total = 0.0;
    datos.map((elemento, id) => {
      const { presupuesto, anio } = elemento;
      total += presupuesto;
      this.etiquetas.push(anio);
      this.valores.push(presupuesto / 1000000);
      this.datos.push({
        id,
        presupuesto: new Intl.NumberFormat().format(presupuesto / 1000000),
        anio,
        valor: presupuesto,
      });
    });
    this.total = new Intl.NumberFormat().format(total);
    this.items = this.datos;
    this.valores = [{ data: this.valores, label: "Presupuesto Autorizado" }];
    console.log(this.valores);
    this.formatTable();
  }

  formatTable() {
    this.grafica.chart.data.datasets[0].data = this.valores;
    this.grafica.chart.data.labels = this.etiquetas;
    this.grafica.chart.update();
  }

  ngOnInit(): void {}
  public SavePDF(): void {
    let content = this.content.nativeElement;
    let doc = new jsPDF();
    let _elementHandlers = {
      "#editor": function (element, renderer) {
        return true;
      },
    };

    doc.fromHTML(content.innerHTML, 5, 5, {
      width: 190,
      elementHandlers: _elementHandlers,
    });
    doc.save("GastoPresupuesto.pdf");
  }

  public SaveCSV(): void {
    const fields = ["presupuesto", "anio"];
    const headers = { fields };
    const csv = parse(this.datos, headers);
    let blob = new Blob([csv], { type: "text/csv" });
    saveAs(blob, "Tabla_Presupuesto.csv");
  }

  comparar() {
    let total = 0.0;
    let etiquetas = [];
    let valores = [];
    if (this.selected.length === 0) {
      if (this.datos.length === 0) {
        this.datos = this.items;
      }
    } else {
      this.datos = this.selected;
    }
    this.datos.map((elemento) => {
      const { valor, anio } = elemento;
      total += valor;
      etiquetas.push(anio);
      valores.push(valor / 1000000);
    });
    this.total = new Intl.NumberFormat().format(total);
    this.etiquetas = etiquetas;
    this.valores = [{ data: valores, label: "Presupuesto Autorizado" }];
    this.formatTable();
  }
}
